.questions{
    padding: 0 200px;
    box-sizing: border-box;
    margin-top: -6px;

    &__wrapper{
        display: flex;
        
    }

    &__image{
        margin-top: -18px;
        margin-left: 0px;
    }

    &__right{
        margin-top: 13px;
    }

    &__left{
        margin-right: 17px;
        display: flex;
        flex-direction: column;
        max-width: 723px;
        h1{
  
margin: 0;
padding: 0;
font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 60px;
line-height: 78px;
text-align: center;
letter-spacing: 0.02em;
     background: linear-gradient(143.85deg, #FFB78F 11.79%, #DB73FF 85.69%);

     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
     background-clip: text;
     text-fill-color: transparent;
        }

    }

    &__block{
      position: relative;
        padding-left: 17px;
        padding-top: 30px;
        padding-bottom: 33px;
        padding-right: 21px;
        width: 780px;
       
        margin-bottom: 28px;
       
        /* Questions card */

box-sizing: border-box;
/* main/black */
background: #0D0D0D;
box-shadow: 2px 2px 20px rgba(239, 57, 255, 0.3);
border-radius: 10px;

&-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
&-parag{
    /* Заполнить заявку указав ваше ФИО, контактную информацию и описание проекта на которое хотите получить финансирование. */

font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 19px;
line-height: 180%;
/* or 34px */

/* main/text/2 */
color: #959595;
margin: 0;
margin-left: -6px;
margin-top: 19px;


}

        &-text{
            h3{
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 500;
                font-size: 28px;
                line-height: 34px;
                margin: 0;
                /* main/white */
                color: #FFFFFF;
            }



        }

        &-plus{
            background: none;
            border: none;
            margin-top: 4px;
        }
    }

    &__block::before{
        content: "";
        position: absolute;
        inset: 0;
        border-radius: 10px;
        padding: 1px; /* control the border thickness */
        background:linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);;
        -webkit-mask: 
          linear-gradient(#fff 0 0) content-box, 
          linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor;
                mask-composite: exclude;
        pointer-events: none;
    }
}

@media screen and (max-width:1700px) {
    .questions{
        &__wrapper{
            justify-content: center;
        }
        &__block{
            max-width: 690px;
        }
    }
}
@media screen and (max-width:1530px) {
    .questions{
        &__block{
            max-width: 690px;
        }
        &__image{
            img{
                width: 605px;
            }
        }
    }
}

@media screen and (max-width:1330px) {
    .questions__left h1 {
        font-size: 53px;
    }
    .questions{
        &__block{
            max-width: 447px;
        }
        &__image{
            img{
                width: 495px;
            }
        }
    }
    .questions__block-text h3 {
        font-size: 21px;
        
    }
    .questions__block-parag {
        font-size: 15px;
    }
}

@media screen and (max-width:1024px) {
    .questions__block {
        max-width: 668px;
    }
    .questions__right {
        margin-top: 0;
    }
    .questions__left {
       margin-right: 0;
        
    }
    .questions__wrapper {
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .questions {
       padding: 0px;
    }
    .questions__image {
        text-align: center;
    }
}
@media screen and (max-width:710px) {
    .questions__block {
        max-width: 550px;
    }
}
@media screen and (max-width: 550px) {
    .questions__left h1 {
        font-size: 32px;
    }
    .questions__image img {
        width: 400px;
    }
    .questions__block {
        max-width: 457px;
    }
}

@media screen and (max-width:500px) {
    .questions__left h1 {
        font-size: 27px;
    }
    .questions__image img {
        width: 320px;
    }
    .questions__block {
        max-width: 360px;
    }
    .questions__block-text h3 {
        font-size: 16px;
    }
}

@media screen and (max-width:415px) {
    .questions{
        margin-top: 29px;
    }
    .questions__left h1{
font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 30px;
line-height: 39px;
text-align: center;
letter-spacing: 0.02em;




    }

    .questions__image {
        margin-top: 0px;
        margin-left: 0px;

        img{
            width: 100%;
        }
    }
    .questions__right {
        margin-top: -1px;
    }
    .questions__block {
        max-width: 305px;
        padding-left: 15px;
        padding-top: 8px;
        letter-spacing: -0.2px;
        padding-bottom: 8px;
        padding-right: 7px;
        margin-bottom: 10px;
    }

    .questions__block-text h3{


font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
/* identical to box height */

/* main/white */
color: #FFFFFF;


    }

    .questions__block-parag {
      

font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;

/* main/text/2 */
color: #959595;
margin-left: -5px;
letter-spacing: 0px;
margin-top: 11px;


    }

    .questions__block:nth-child(3){
        padding-bottom: 13px;
    padding-top: 10px;
    }
    .questions__block:last-child{
        padding-top: 13px;
        padding-bottom: 11px;
        letter-spacing: -0.23px;
    }
}

@media screen and (max-width: 350px) {
    .questions__block {
        max-width: 288px;
    }
}