.person{
    max-width: 280px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
box-sizing: border-box;
background: #0D0D0D;
box-shadow: 2px 2px 9px #91009D;
border-radius: 10px;
padding-bottom: 18px;
position: relative;
    &__image{
        width: 100%;
        max-height: 275px;
    }

    &__text-name{
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 23px;
line-height: 28px;
text-align: center;
margin-top: 18px;
margin-bottom: 15px;
color: #FFFFFF;

    }

    &__text-position{
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
margin: 0;
margin-bottom: 33px;
line-height: 17px;
text-align: center;
color: #959595;

    }

    &__socials{
        margin: 0;
        padding: 0;
        margin-top: 1px;
    margin-left: 0px;
        display: flex;
        &-item:not(:last-child){
            margin-right: 20px;
        }
    }
    
}

.person::before{
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 10px;
    padding: 1px; /* control the border thickness */
    background:linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);;
    -webkit-mask: 
      linear-gradient(#fff 0 0) content-box, 
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
            mask-composite: exclude;
    pointer-events: none;
}

@media screen and (max-width:415px) {
    .person {
        max-width: 132px;
        margin-left: 0px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-sizing: border-box;
        background: #0D0D0D;
        box-shadow: 2px 2px 9px #91009D;
        border-radius: 10px;
        padding-bottom: 18px;
        position: relative;
        max-height: 200px;
    }
    .person:not(:last-child){
        margin-left: 0px;
        margin-right: 7px;
    }
    .person__image {
        border-radius: 10px;
        width: 100%;
        max-height: 129px;
        img{
            height: 100%;
            width: 100%;
        }
    }
    .person::before {
        content: "";
        position: absolute;
        inset: 0;
        border-radius: 3px;
        padding: 1px;
        background: linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);
        -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
        pointer-events: none;
    }
    .person__text-name {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 28px;
        text-align: center;
        margin-top: 2px;
        letter-spacing: 0.3px;
        margin-bottom: -6px;
        color: #FFFFFF;
    }
    .person__text-position {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 9px;
        margin: 0;
        margin-bottom: 5px;
        line-height: 17px;
        text-align: center;
        color: #959595;
    }
    .person__socials-item:not(:last-child) {
        margin-right: 12.14px;

        img{
            width: 11.72px;
        }
    }

    .person__socials-item{
        img{
            width: 11.72px;
        }
    }
}

@media screen and (max-width:350px) {
    .person:not(:last-child) {
        margin-left: 0px;
        margin-right: 7px;
    }
}