.persons{
    padding-top: 136px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-bottom: 55px;
    box-sizing: border-box;
    overflow: hidden;
    &__title{
        width: 100%;
        h1{
            margin: 0;
            padding: 0;
font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 60px;
line-height: 78px;
text-align: center;

/* main/white */
color: #FFFFFF;


        }
    }

    &__btn{
box-sizing: border-box;
padding: 18px 25px;
border: 2px solid #FFFFFF;
border-radius: 50px 0px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 22px;
line-height: 27px;
color: #FFFFFF;
width: 133px;
background: none;
position: relative;
text-align: left;
    }

    &__btn::before{
        top: 25px;
    left: 91px;
        content: '';
        width: 18px;
        height: 15px;
        background: url('../../images/arrow-bottom.svg');
        position: absolute;
    }

    &__items{
        display: flex;
    row-gap: 37px;
    column-gap: 47px;
        flex-wrap: wrap;
        margin-top: 50px;
        margin-bottom: 71px;
        justify-content: center;
        max-width: 1261px;
    }
}

.blur-left{
    /* Ellipse 15 */
z-index: -1;
position: absolute;
width: 585px;
height: 585px;
background: linear-gradient(180deg, #8201AF 65.71%, #0008CC 100%);
filter: blur(250px);
left: -94px;
    top: 310px;


}

.blur-right{
    /* Ellipse 16 */
    z-index: -1;
position: absolute;
width: 585px;
height: 855px;
background: linear-gradient(180deg, #8201AF 65.71%, #0008CC 100%);
filter: blur(250px);
right: -300px;
    top: 260px;

}

.swiper-persons{
    display: none;
}

@media screen and (max-width:710px) {
    .persons__items {
        row-gap: 10px;
        column-gap: 15px;
    }
    
}
@media screen and (max-width: 550px) {
    .persons__items {
        row-gap: 30px;
        column-gap: 15px;
    }
}

@media screen and (max-width:415px) {
    .persons__items{
        display: none;
    }
    .persons {
        padding-top: 99px;
        display: flex;
       
        flex-direction: column;
        align-items: center;
        position: relative;
        padding-bottom: 55px;
        box-sizing: border-box;
        overflow: hidden;
    }
    .persons__title {
        width: 100%;
        padding-left: 3px;
        h1{
          
                margin: 0;
                padding: 0;
                font-family: "Chakra Petch";
                font-style: normal;
                font-weight: 700;
                font-size: 30px;
                line-height: 78px;
                text-align: center;
                color: #FFFFFF;
            
        }
    }
    .persons{
        .swiper-persons{
            display: flex;
           width: 100%;
            
            margin-left: auto;
            margin-right: auto;
            position: relative;
            overflow: hidden;
            list-style: none;
            padding: 0;
            z-index: 1;
            display: block;
           
            box-sizing: border-box;
            margin-top: 3px;
            padding-left: 0px;
        }

        .swiper-slide{
            margin: 0 !important;
        }
    }
    .persons__btn{
    display: none;
    
    }
    .swiper-twoSlides{
        display: flex;
        justify-content: center;
    }
    .slider-next-btn{
        position: absolute;
    right: 16px;
    z-index: 1000;
    top: 78px;
    }
    .slider-prev-btn{
    position: absolute;
    top: 78px;
    left: 16px;
    z-index: 1000;
    }
}

@media screen and (max-width:350px) {
    .persons .swiper-persons {
        padding-left: 0px;
    }
    
    .slider-next-btn {
      
        right: 2px;
        img{
            width: 16px;
        }
        
    }
    .slider-prev-btn {
      
        left: 2px;
       img{
        width: 16px;
       }
    }
}