.block{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 350px;
    position: relative;
    margin-right: 40px;
    padding: 23px 0px;
    box-shadow: 2px 2px 9px #91009D;
   
    border-radius: 60px;
    &__title{
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 28px;
line-height: 34px;
text-align: center;
margin: 0;
padding: 0;
color: #FFFFFF;
margin-top: 6px;

    }

    &__text{
        height: 114px;
        max-width: 299px;
        width: 100%;
        margin: 0;
        padding: 0;

font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
text-align: center;
letter-spacing: 0.02em;
color: #FFFFFF;

margin-top: 18px;
margin-bottom: 8px;
margin-left: 8px;
padding: 0 30px;


    }

    &__btn{
        margin-top: 2px;
    margin-left: 1px;
       
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 24px;
background: linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
background-clip: text;
text-fill-color: transparent;
border: none;

    }
}

.block:last-child{
    margin: 0;
}

.block:nth-child(4){
    .block__text{
        margin-top: 19px;
        margin-left: 10px;
        letter-spacing: 0.03rem;
        width: 299px;
        height: 104px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 21px;
        text-align: center;
        margin-bottom: 16px;
    }
}
.block:nth-child(2){
    .block__text{
        margin-top: 19px;
        margin-left: 10px;
        letter-spacing: 0.03rem;
        width: 299px;
        height: 104px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 21px;
        text-align: center;
        margin-bottom: 16px;
    }
}

.block::before{
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 60px;
    padding: 1px; /* control the border thickness */
    background:linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);;
    -webkit-mask: 
      linear-gradient(#fff 0 0) content-box, 
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
            mask-composite: exclude;
    pointer-events: none;
}




@media screen and (max-width:1540px) { 
    
    .block{
        max-width: 300px;
        img{
            width: 60px;
        }
        &__title{
            font-size: 24px;
        }

        &__text{
            font-size: 16px;
            margin-top: 4px;
            height: 94px;
            box-sizing: border-box;
        }

        &__btn{

        }
    }

    .block:nth-child(4){
        .block__text{
            margin-top: 4px;
