.tokenomic{
    position: relative;
    &__wrapper{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 181px;
    }

    &__title{
margin: 0;
margin-left: -2px;

font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 69px;
line-height: 84px;
color: #fff;

span{
    background: linear-gradient(117.47deg, #86AFFE 7.39%, #A57AFF 23.36%, #FB84FF 38.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}




    }

    &__subtitle{
        margin-left: 7px;
        margin-top: 22px;
        letter-spacing: -0.1px;
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 155%;
        text-align: center;
        max-width: 1120px;
        color: #FFFFFF;
        span{
            margin-right: 4px;
        }

    }

    &__image{
        margin-left: -2px;
        margin-top: 39px;
    }

    .blur-token{
        /* Vector */

position: absolute;
width: 567px;
height: 722px;
left: -303px;
top: 12px;
/* accent/5 */
background: linear-gradient(145.54deg, #7673FF 10.4%, #42F4FF 92.38%);
filter: blur(250px);

    }
}

.tokenomic__image-mobile{
    display: none;
}

@media screen and (max-width:1440px) {
    .tokenomic__subtitle {
        margin-left: 0px;
        padding: 0px 10px;
    }
    .tokenomic__image {
        max-width: 1000px;
        img{
            width: 100%;
        }
    }
}

@media screen and (max-width: 1000px) {
    .tokenomic__image {
        max-width: 700px;
    }
}

@media screen and (max-width:760px) {
    .tokenomic__title {
        text-align: center;
        font-size: 49px;
        margin-top: 0;
    }
}

@media screen and (max-width: 550px) {
    .tokenomic__image {
        max-width: 370px;
    }
    .token .footer .header__left {
        margin-top: -119px;
        margin-left: -4px;
    }
}

@media screen and (max-width:415px) {
    .tokenomic__title {
        font-size: 30px;

line-height: 37px;


    }

    .tokenomic__subtitle{

        
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 19px;

        text-align: center;
        span{
            margin: 0;
            padding: 0;
        }
        color: #FFFFFF;
        
        
margin: 0;
max-width: 300px;
padding: 0;
margin-top: 10px;
margin-left: -1px;
margin-top: 10px;
letter-spacing: 0.05px;
    }

    .tokenomic__wrapper {
        margin-top: 79px;
    }
    .tokenomic__image-mobile{
        display: block;
    }
    .tokenomic__image-desktop{
        display: none;
    }
}

@media screen and (max-width: 350px) {
    .tokenomic__image {
        max-width: 300px;
    }
}