.main{
    width: 100%;
    box-sizing: border-box;
    padding-left: 198px;
    margin-top: 104px;
    &__wrapper{
        display: flex;
       
        
    }
    &__title{
       margin: 0;
       padding: 0;
font-family: 'Chakra Petch';
font-style: normal;
font-weight: 600;
font-size: 147px;
line-height: 191px;
background: linear-gradient(103.59deg, #54D6FF 13.23%, #C275FF 56.28%, #F36AFF 91.95%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
background-clip: text;
text-fill-color: transparent;


    }

    &__subtitle{
        margin: 0;
        padding: 0;
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 48px;
line-height: 59px;
max-width: 780px;
color: #FFFFFF;
margin-top: 20px;
    }

    &__text{
margin: 0;
padding: 0;
font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 37px;
line-height: 45px;
color: #959595;
max-width: 810px;
margin-top: 24px;
margin-bottom: 44px;
    }
    &__coin-image{
        margin-left: 39px;
    }

}

@media screen and (max-width:1330px) {
    .main{
        padding: 0px;

        .main__wrapper{
            align-items: center;
            flex-direction: column-reverse;
        }
     
    }
}

@media screen and (max-width:1024px) {
    .main{
      &__title{
        text-align: center;
        font-size: 113px;
        line-height: 132px;
      }
      .header__buttons {
        display: flex;
        margin-top: -5px;
        margin-right: 1px;
        justify-content: center;
    }
    .header__btn {
        padding: 19px 34px;
        font-size: 22px;
    }

      &__coin-image{
        margin: 0;
      }

      &__subtitle{
        margin-top: 10px;
        font-size: 33px;
        line-height: 33px;
        text-align: center;
        
      }

      &__text{
        font-size: 25px;
        line-height: 39px;
        text-align: center;
        margin-top: 10px;
        margin-bottom: 35px;
      }
     
    }
}

@media screen and (max-width:710px) {
  .main{
    &__title{
      text-align: center;
      font-size: 61px;
      line-height: 132px;
    }
    .header__buttons {
      display: flex;
      margin-top: -5px;
      margin-right: 1px;
      justify-content: center;
  }
  .header__btn {
      padding: 19px 34px;
      font-size: 22px;
  }

    &__coin-image{
      margin: 0;
    }

    &__subtitle{
      margin-top: -24px;
      font-size: 19px;
      
    }

    &__text{
      font-size: 18px;
      line-height: 39px;
      text-align: center;
      margin-top: 2px;
    }

    &__coin-image img{
      max-width: 550px;
    }
   
  }
}

@media screen and (max-width:550px) {
  .main__coin-image img {
    max-width: 400px;
}
.main__title {
  font-size: 51px;
}
.main__text {
  font-size: 17px;
  line-height: 30px;
  text-align: center;
  margin-top: 2px;
}


}

@media screen and (max-width:500px) {
  .main {
    margin-top: 20px;
}
.main__coin-image img {
  max-width: 256px;
}

.main__title{

font-family: 'Chakra Petch';
font-style: normal;
font-weight: 600;
font-size: 55.5777px;
line-height: 72px;

/* accent/2 */
background: linear-gradient(103.59deg, #54D6FF 13.23%, #C275FF 56.28%, #F36AFF 91.95%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
background-clip: text;
text-fill-color: transparent;
margin-bottom: 22px;

}

.main__subtitle{



font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 22px;
line-height: 27px;
text-align: center;

/* main/white */
color: #FFFFFF;


}


.main__left{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main__subtitle {
  font-size: 22px;
  line-height: 27px;
  text-align: center;
