@media screen and (max-width:415px) {
  .main {
    margin-top: 33px;
}
.main__wrapper{
  margin-left: 3px;
}
.main__title{
  margin-top: 17px;
  margin-left: 5px;
  letter-spacing: -0.035px;
  margin-bottom: 34px;
}
.main__subtitle {
  margin-bottom: 7px;
}
.main__text {
  margin-bottom: 28px;
}
.main .header__btn {
  padding: 13px 18px;
  font-size: 19px;
  letter-spacing: 0.3px;
  margin-right: 21px;
}
.main .header__btn-styles{
  margin: 0;
}
.main .header__buttons {
  display: flex;
  margin-top: -6px;
  margin-right: 1px;
  justify-content: flex-start;
  width: 100%;
  padding-left: 13px;
  box-sizing: border-box;
}
.main .header__btn-styles {
  margin: 0;
  padding: 13px 22px;
  letter-spacing: 0.28px;
 padding-left: 0;

