        position: unset;
        width: 100%;
        margin-left: 0px;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .fundTree__block {
        position: relative;
        top: 0;
        left: 0;
        right: 0;
        background: none !important;
        padding: 0;
        padding: 20px 0px;
        padding-left: 18px;
        max-width: 245px;
        width: 100% !important;
        box-sizing: border-box;
        border: 1px solid white;
        border-radius: 20px;
        height: 144px !important;
       
        
       &-title {
        
        color: #fff;
font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 25px;
line-height: 32px;
margin: 0 !important;
margin-bottom: 28px !important;


    }
    &-subtitle {
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 16px;
max-width: 220px;

        color: #fff;
    }


}

.fundTree__block::before{
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 20px;
    padding: 1px;
    background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%);
    -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    pointer-events: none;
}


.fundTree__block:first-child,.fundTree__block:nth-child(2),.fundTree__block:nth-child(3){
    &::after{
        content: '';
        background: url('./../images/btn-bottom.png');
        width: 45px;
        bottom: -54px;
        height: 45px;
        left: 95px;
        position: absolute;
        background-repeat: no-repeat;
    }
    margin-bottom: 39px;
    position: relative;
    top: 0;
    left: 0;
    right: 0;

    
}

.fundTree__block:last-child{
    margin-top: 8px;
    background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%) !important;
    border-radius: 20px;
    top: 0;
    left: 0;
    right: 0;
}

.fundTree__block:nth-child(4){
    background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%) !important;
    border-radius: 20px;
    top: 0;
    right: 0;
    left: 0;
}



}

