        }
        
}
.header__container{
  display: flex;
  justify-content: space-between;
  padding-left: 60px;
 
 

}
.header__left{
  display: flex;
  align-items: center;

  img{
    width: 68px;
    margin-right: 15px;
  }
}

.header__right{
  display: flex;
  align-items: center;
  margin-top: -20px;
  img{
    width: 68px;
    margin-right: 15px;
  }
}

.close-btn{
  display: none;
  }
  .burger-btn{
    display: none;
  }
@media screen and (max-width:1500px) {
  .header{
    &__name{
      font-size: 30px;
    }
    &__container{
      padding-left: 10px;
    }
    &__left{
      padding-left: 10px;
      img{
        width: 47px;
      }
    
    }

    &__btn-styles{
      margin-right: 10px;
    }

    &__right{
      margin-top: -7px;
    }
  }
}

