@media screen and (max-width:1023px) {
  .header__nav{
      position: absolute;
      left: 0px;
      width: 300px;
      top: 0px;
      height: 100vh;
      background: linear-gradient(145.54deg, #7673FF 10.4%, #42F4FF 92.38%);
      border: none;
      padding-top: 0px;
      bottom: 0px;
      padding-bottom: 0px;
      z-index: 10000;
      transform: translateX(-300px);
      transition: all ease 1s;
  }

  .header__logo{
      display: none;
  }
  .header__name{
    display: none;
  }

  .header__list{
      flex-direction: column;
      &-item{
          padding: 23px;
          margin: 0px;
          border-bottom: 1px solid #5d5d5d;

          
      }
  }
  .header__list-item:hover{
      background: #5d5d5d;
  }

  .header__list-link {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 23px;
      line-height: 30px;
      text-decoration: none;
      color: #FFFFFF;
   
      letter-spacing: -0.12px;
  }



  .header__container{
      justify-content:space-between;
      padding-left: 50px;
  }


  .close-btn{
    display: block;
    width: 24px !important;
    position: absolute;
    top: 10px;
    left: 263px;
  }
  .burger-btn{
    display: block;
    width: 56px;
  }
}

@media screen and (max-width:480px) {
  

  .header__buttons{
    display: none;
  }
  .header__language {
    display: none;

}

.header__logo{
  display: block;
}

.header__name{
  display: block;
}
.burger-btn {
  position: absolute;
  top: 30px;
  right: 20px;
}
.header__container{
  padding-left: 20px;
}

}

@media screen and (max-width:415px) {
