.imageBlock{
    display: flex;
    width: 100%;
    margin-top: 136px;
    justify-content: center;
    flex-direction: column;
    &__title{
        h1{

font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 60px;
line-height: 78px;
/* identical to box height */
text-align: center;

/* main/white */
color: #FFFFFF;


        }
    }

    &-img{
        display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 41px;
    }
}

@media screen and (max-width:1530px) {
    

    .imageBlock-img img{
        max-width: 1000px;
    }

}
@media screen and (max-width: 1200px) {
    .imageBlock-img img {
        max-width: 800px;
    }
}

@media screen and (max-width:1024px) {
    .imageBlock__title h1 {
        font-family: "Chakra Petch";
        font-style: normal;
        font-weight: 700;
        font-size: 53px;
        line-height: 78px;
        text-align: center;
        color: #FFFFFF;
    }
    .imageBlock-img img {
        max-width: 630px;
    }
}

@media screen and (max-width:710px) {
    .imageBlock-img img {
        max-width: 550px;
    }
}

@media screen and (max-width:550px) {
    
        .imageBlock__title h1 {
            font-size: 36px;
        }
        .imageBlock-img img {
            max-width: 400px;
        }
        .imageBlock {
            margin-top: 0px;
        }
    
}

@media screen and (max-width: 500px) {
    .imageBlock__title h1 {
        font-size: 30px;
    }
    .imageBlock-img img {
        max-width: 360px;
    }
}

@media screen and (max-width:415px) {
    .imageBlock__title h1{
        font-size: 21px;
        padding-left: 10px;
        margin-top: 20px;
    }
    .imageBlock-img {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: -3px;

        img{
            width: 331px;
        }
    }
}

@media screen and (max-width:350px) {
    .imageBlock-img img {
        width: 290px;
    }
}