
.footer{
    margin-left: 122px;
    margin-right: 309px;
    margin-top: 272px;
    position: relative;
padding-bottom: 30px;
    &__image{
        position: absolute;
width: 134.07px;
height: 144px;
right: -210px;
top: 18px;
background: linear-gradient(103.59deg, #FC84FF 13.23%, #54D1FA 91.95%);
filter: blur(31.0345px);
z-index: 100;

    }

    .img{
        position: absolute;
        height: 144px;
right: -210px;
z-index: 1000;
top: 18px;
width: 124px;
height: 110px;
    }
   

    .header__language{
        margin-top: 3px;
    }
    .wrapper{
        display: flex;
        justify-content: space-between;
        margin-top: 2px;
    }
    .header__right{
        justify-content: space-between;
    }
    &__block{
        display: flex;
        justify-content: space-between;
        margin-top: 68px;
    }
    &__text{
max-width: 999px;
p{
font-family: 'Montserrat'; font-style: normal; font-weight: 500; font-size: 17px; line-height: 21px; /* main/text/2 */ color: #959595;

margin: 0;
margin-top: -4px;
letter-spacing: -0.23px;

span{
    letter-spacing: -0.2px;
    margin-left: -4px;
    
}
}



    }

    .header__name{
        font-size: 31.52px;
    }
    .header__left{
        margin-top: -19px;
        margin-left: -4px;
        img{
            width: 60px;
        }
    }
}


@media screen and (max-width:1530px) {
    .footer{
        margin-left: 30px;
        margin-right: 72px;
       .img {
            right: -46px;
            top: 49px;
            width: 91px;
            height: 75px;
        }
        &__image {
            width: 108.07px;
            height: 116px;
            right: -54px;
            top: 23px;
        }
    }
    
}

@media screen and (max-width:1330px) {
    .footer__text {
        max-width: 650px;
        font-size: 14px;
    }
    .footer .header__name {
        font-size: 22.52px;
    }
    .footer__image {
        width: 108.07px;
        height: 116px;
        right: -69px;
        top: 25px;
    }
    .footer .img {
        right: -64px;
        top: 40px;
        width: 91px;
        height: 75px;
    }
}

@media screen and (max-width:1024px) {
    .footer{
     
        margin-right: 4px;
    
    }
    .footer__text {
        max-width: 100%;
        font-size: 14px;
        margin-top: 25px;
        margin-left: 10px;
    }
    .footer .header__logo {
        display: block;
    }
    .footer .header__name {
        display: block;
    }
    .footer__block {
        display: block;
        justify-content: space-between;
        margin-top: 68px;
    }
    .footer .header__left {
        margin-top: -119px;
        margin-left: -4px;
    }
    .footer .header__right {
        justify-content: flex-end;
    }
    .footer__image {
      display: none;
    }
    .footer .img {
        display: none;
    }

    .footer .header__nav{
        display: none;
    }
}
@media screen and (max-width:630px) {
   
    .footer {
        margin-left: 10px;
        margin-right: 0px;
    }
  .footer .header__language {
    display: none;

}
  }
@media screen and (max-width:550px) {
    
   
    .footer .header__right{
        justify-content: space-between;
    }
    .footer .header__left img {
        width: 40px;
    }
    .footer .header__name {
        font-size: 19.52px;
    }
}

@media screen and (max-width:500px) {
    

    .footer .header__nav {
       display: block;
       position: relative;
       background: none;
       transform: translateX(0px);
       width: 100%;
       text-align: center;
       height: 100%;
    }
    .footer .header__list-item {
        padding: 23px;
        margin: 0px;
        border-bottom: none;
    }
    .footer .header__left {
        display: flex;
     }
     .footer .header__right {
        justify-content: center;
        flex-direction: column;
    }
    .footer {
       margin-left: 0;
       margin-top: 130px;
    }
    .footer__block {
        margin-top: 0px;
    }
    .footer .header__left {
       position: absolute;
       top: 59px !important;
       left: 28% !important;
   
    }
    .footer__text p {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 21px;
        color: #959595;
        margin: 0;
        margin-top: -4px;
        margin-left: 12px;
        letter-spacing: -0.23px;
    }


}

@media screen and (max-width:415px) {
    .footer .header__right {
        flex-direction: column;
    }

    .footer .header__buttons {
        display: block;
    }
    .footer {
       padding: 0;
        margin-top: 65px;
        box-sizing: border-box;
        margin-right: 0;
    }

    .footer .header__name {
        font-size: 15px;
        margin-bottom: 0;
        margin-left: 9px;
    }
    .footer .header__left img {
        height: 27px;
        margin-right: -4px;
        width: 30px;
    }
    .footer .header__left {
        position: absolute;
        top: 68px !important;
        left: 35% !important;
    }
    .footer .header__list-item{
        padding: 0;
        margin-bottom: 10px;
        
    }
    .footer .header__list-link{

font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
color: #FFFFFF;

    }
    .footer .header__btn{


font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 20px;
color: #FFFFFF;
padding: 14px 15px;

    }

    .footer .header__buttons{
        margin-top: 12px;
        margin-left: 14px;
    
    }
    .footer__text{
        margin: 0;
        margin-top: 23px;
 
       margin-left: 0;
     
       display: flex;
       justify-content: center;
    }

    .footer__text p{

margin: 0;
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 9px;
line-height: 11px;
text-align: center;
max-width: 276px;
color: #959595;
letter-spacing: 0px;

span{
    margin: 0;
    letter-spacing: 0px;
}
    }

    .footer .header__nav {
       margin: 0;
    }
}