.blocks{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 84px;
    &__items{
        display: flex;
        justify-content: center;
        margin-top: 40px;
    }
    &__title{

font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 60px;
line-height: 78px;
text-align: center;
margin: 0;
padding: 0;
color: #FFFFFF;
padding-right: 53px;
    box-sizing: border-box;
span{
    color: #EF6AFE;
}
    }
}

.swiper-blocks{
    display: none;
}

@media screen and (max-width:1330px) {
    .blocks{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 84px;
     
        &__items{
           flex-wrap: wrap;
           gap: 53px;
        }
    }
}

@media screen and (max-width:710px) {
    .blocks__title {
        font-size: 51px;
        padding-right: 0;
    }
}

@media screen and (max-width:550px) {
    .blocks__title {
        font-size: 38px;
        padding-right: 0;
    }
}

@media screen and (max-width:500px) {
    .blocks__title {

font-family: 'Chakra Petch';
font-style: normal;
font-weight: 700;
font-size: 24px !important;
line-height: 31px;
text-align: center;

/* main/white */
color: #FFFFFF;


    }

}

@media screen and (max-width:415px) {
    .blocks {
        margin-top: 130px;
        .swiper-slide:nth-child(2){
            .block__text{
                margin-bottom: 7.5px;
            }
        }
        .swiper-slide{
            width: 100% !important;
            margin-left: 0 !important;
            
        }
    }
    .blocks__items {
        display: none;
      justify-content: flex-start;
        margin-top: 31px;
        margin-left: 51px;
    }

    .swiper-blocks{
        display: flex;
       width: 100%;
     
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        list-style: none;
        padding: 0;
        z-index: 1;
        display: block;
       
        box-sizing: border-box;
        margin-top: 25px;
        padding-left: 53px;
    }
  
    .blocks__title{
      
        margin-left: 6px;
    }
}

@media screen and (max-width:350px) {
    .swiper-blocks {
        padding-left: 24px;
    }
}

