.submitBlock{
    display: flex;
    justify-content: center;
    position: relative;
    margin-top: 317px;

   position: relative;

    &__wrapper{
        display: flex;
        max-width: 1720px;
        flex-direction: column;
        align-items: center;

    }

  

    &__title{

margin: 0;
font-family: 'Montserrat';
font-style: normal;
font-weight: 700;
font-size: 50px;
line-height: 61px;
text-align: center;

color: #FFFFFF;


    }
    &__text{
        position: relative;
    }
    &__text::before{
        content: '';
        background: url('../../images/cornersvg.svg');
        position: absolute;
        width: 69px;
        height: 69px;
        right: -63px;
        top: -54px;
    }
    &__text::after{
        content: '';
        background: url('../../images/cornersvg.svg');
        transform: rotate(180deg);
        
        position: absolute;
        width: 69px;
        height: 69px;
        left: -40px;
        bottom: -49px;
    }

    .submit-blur{
        /* Vector */

position: absolute;
width: 337px;
height: 451px;
right: -152px;
    top: -687px;
    z-index: -1;
/* accent/4 */
background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%);
filter: blur(150px);
transform: rotate(180deg);

    }

    &__buttons{
        display: flex;
        
        margin-top: 93px;
        margin-left: 51px;

        .main__token-btn{
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 33.0512px;
line-height: 40px;
filter: drop-shadow(2.36275px 2.36275px 23.6275px rgba(239, 57, 255, 0.5)); border-radius: 0px 67.6047px;

width: 323px;
/* main/white */
color: #FFFFFF;
margin-right: 31px;
height: 83px;
border-radius: 0px 60px;

        }

        .main__token-btn::before{
            background: linear-gradient(103.59deg, #54D6FF 13.23%, #C275FF 56.28%, #F36AFF 91.95%); 
            border-radius: 0px 60px;
        }

        .main__token-btn:last-child{
            margin-right: 0;
        }
    }

    &__socials{
        display: flex;
        margin-left: 81px;
        margin-top: 106px;
        padding: 0;
        
    }

    &__social{
        width: 51px;
        height: 50px;
       
            margin-right: 86px;
        
        
        img{
            width: 100%;
            height: 100%;
        }
    }
    .tokens {
        position: absolute;
        left: -62px;
        top: -217px;
    }
    .tokens-right {
        position: absolute;
        right: 0px;
        top: -361px;
    }

   
}

@media screen and (max-width:1920px) {
    .submitBlock .tokens-right {
       max-width: 400px;
    }

    .submitBlock .tokens{
        max-width: 400px;
        left: -6px;
    }


}

@media screen and (max-width:1500px) {
    .submitBlock .tokens-right {
       max-width: 280px;
    }

    .submitBlock .tokens{
        max-width: 280px;
     
    }


}

@media screen and (max-width:1230px) {
    .submitBlock .tokens {
        top: -308px;
    }
}

@media screen and (max-width:1000px) {
    .submitBlock {
        margin-top: 200px;
    }
    .submitBlock__title{
        font-size: 39px;
    }
    .submitBlock__text::before {
        right: -10px;
    }
    .submitBlock__text::after {
        left: -10px;
    }
    .submitBlock .tokens-right {
        right: 1px;
        top: -219px;
        max-width: 160px;
    }
    .submitBlock .tokens {
        top: 0px;
        max-width: 161px;
        left: -6px;
    }
}

@media screen and (max-width:760px) {
    .submitBlock__socials{
        flex-wrap: wrap;
        justify-content: center;
        max-width: 500px;
        gap: 30px;
    }
    .submitBlock__text::before {
        right: -20px;
    }
    .submitBlock__buttons .main__token-btn {
        font-size: 25.0512px;
        width: 262px;
    }
    .submitBlock__text::after {
        left: -20px;
    }
}

@media screen and (max-width:690px) {
    .submitBlock__title {
        font-size: 30px;
    }
    .submitBlock__buttons {
        margin-top: 25px;
        margin-left: 0;
        justify-content: center;
    }

    .submitBlock .main__token-btn{
       
            font-size: 20.0512px;
            width: 200px;
   
    }
    .submitBlock__social {
        margin-right: 0px;
    }
    .submitBlock__socials {
        margin-left: 0;
    }
}

@media screen and (max-width:550px) {
    .submitBlock__title{
        display: none;
        
    }
    .submitBlock__text::before{
        right: -7px;
        top: 8px;
        width: 30px;
        height: 30px;
        background-size: cover;
    
    }

    .submitBlock__text::after{
        left: -27px;
        bottom: -19px;
        width: 30px;
        height: 30px;
        background-size: cover;
    }
    .submitBlock .main__token-btn {
        font-size: 18.0512px;
        width: 145px;
        height: 45px;
    }
 .submitBlock__socials {
        margin-left: 0;
        max-width: 280px;
        margin-top: 67px;
    }
    .submitBlock {
        margin-top: 50px;
        border-bottom: 1px solid white;

    }
    .submitBlock .submit-blur {
        position: absolute;
        width: 337px;
        height: 451px;
        right: -152px;
        top: -511px;
        z-index: -1;
        background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%);
        filter: blur(150px);
        transform: rotate(180deg);
    }
}

@media screen and (max-width:415px) {
    .submitBlock .submit-blur {
        position: absolute;
        width: 337px;
        height: 451px;
        right: -300px;
        top: -253px;
        z-index: -1;
        background: linear-gradient(148.71deg, #B2FF0F 10.47%, #76FFEF 88.29%);
        filter: blur(150px);
        transform: rotate(180deg);
    }
    .submitBlock__text::after {
        left: -10px;
        bottom: -25px;
    }
    .submitBlock__text::before {
        right: -10px;
        top: 8px;
    }

    .submitBlock__buttons{
        margin-left: 0;
    }
}

@media screen and (max-width:360px) {
    .submitBlock{
        margin-left: 20PX;
    }
    .submitBlock__text::after {
        left: -20px;
        bottom: -25px;
    }
    .submitBlock__text::before {
        right: 0px;
        top: 8px;
    }
    .submitBlock .main__token-btn {
        font-size: 15.0512px;
        width: 130px;
        height: 45px;
    
    }
    .submitBlock__socials {
        margin-left: 0;
        max-width: 220px;
        margin-top: 67px;
        gap: 10px;
    }
    .submitBlock__social img {
        width: 70%;
        height: 100%;
    }
}