.blocks__token{
    padding-left: 202px;
    margin-top: 170px;
    padding-right: 162px;
    display: flex;
  
}


@media screen and (max-width:1915px) {
    .block__token{
        padding-left: 15px;
        padding-right: 15px;
    }
    .block__token-subtitle {
        margin-left: 0px;
    }
    .block__token:nth-child(2) .block__token-subtitle {
        margin-left: 0px;
    }
    .block__token:nth-child(3) .block__token-subtitle {
        margin-left: 0px;
    }
}

@media screen and (max-width:1370px) {
    .blocks__token{
        margin-top: 170px;
   
    display: flex;
    gap: 36px;
    justify-content: center;
    padding: 0px 20px;
    }
}

@media screen and (max-width:1000px) {
    .blocks__token{
        flex-wrap: wrap;
    }
}

@media screen and (max-width:415px) {
    .blocks__token-items{
        display: none;
    }

    .blocks__token{
        padding-right: 0px;
        padding-left: 0;
    }
  
    .blocks__token {
        margin-top: 39px;
    }
    .token .swiper-blocks {
        display: flex;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        list-style: none;
        padding: 0;
        z-index: 1;
        display: block;
        box-sizing: border-box;
        margin-top: 25px;
        padding-left: 0px;
    }

    .token .swiper-slide:first-child{
        margin-left: 32px !important;
    }

    .token .swiper-slide{
        margin-left: 0 !important;
        margin-right: 28px !important;
        width: 308px !important;
    }
}

@media screen and (max-width:350px) {
    
    .token .swiper-slide {
        margin-left: 0 !important;
        margin-right: 12px !important;
        width: 100% !important;
        display: flex;
        margin: 0 !important;
        justify-content: center;
    }
    .token .swiper-slide:first-child{
       margin: 0 !important;
    }
    .token .swiper__wrapper{
        width: 92%;
    }
}