.form__wrapper{
display: flex;
width: 100%;
align-items: center;
flex-direction: column;
padding-left: 57px;
box-sizing: border-box;
margin-top: 209px;
position: relative;
}

.form__title{
    h1{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 50px;
        line-height: 61px;
        margin: 0;
        
        color: #FFFFFF;
    }
}

.form{



display: flex;
flex-direction: column;
align-items: center;
max-width: 611px;
width: 100%;
margin-top: 37px;
}

.form-input{
    margin-bottom: 16px;
    width: 100%;
    padding-left: 10px;
    padding-bottom: 20px;
    padding-top: 40px;
    box-sizing: border-box;
    background: none;
    border-left: none; 
  border-right: none; 
  border-top: none;
  border-bottom: 3px solid;
  border-image: linear-gradient(103.59deg, #FC84FF 13.23%, #54D1FA 91.95%) 47% 0%;
    &::placeholder{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 28px;
        line-height: 34px;
        color: #959595;
    }



}

.form-btn{

    padding: 0px 73px;
    padding-top: 17px;
    padding-bottom: 16px;
    background: none;
    box-sizing: border-box;
    filter: drop-shadow(2px 2px 20px rgba(239, 57, 255, 0.5));
    border-radius: 0px 60px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 28px;
line-height: 34px;
margin-top: 43px;
color: #FFFFFF;


}

.form-btn::before{
    content: "";
    position: absolute;
    inset: 0;
border-radius: 0px 60px;
    padding: 1px; /* control the border thickness */
    background:linear-gradient(103.59deg, #86AFFE 13.23%, #A57AFF 53.2%, #FB84FF 91.95%);;
    -webkit-mask: 
      linear-gradient(#fff 0 0) content-box, 
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
            mask-composite: exclude;
    pointer-events: none;
}

.tokens{
    position: absolute;
    left: 0px;
    top: -328px;

}
.tokens-right{
    position: absolute;
    right: 0px;
    top: -76px;
}

@media screen and (max-width:1530px) {
    .tokens-right {
        position: absolute;
        right: 8px;
        top: 122px;
        width: 440px;
    }
    .tokens {
        position: absolute;
        left: 0px;
        top: -162px;
        width: 440px;
    }
}
@media screen and (max-width:1530px) {
    .tokens-right {
        position: absolute;
        right: 8px;
        top: 122px;
        width: 320px;
    }
    .tokens {
        position: absolute;
        left: 0px;
        top: -162px;
        width: 320px;
    }
}

@media screen and (max-width:1024px) {
    .tokens {
        position: absolute;
        left: 0px;
        top: -233px;
        width: 320px;
    }
    .tokens-right {
        position: absolute;
        right: 8px;
        top: 337px;
        width: 320px;
    }
}

@media screen and (max-width:710px) {
    .form__wrapper {
        padding-left: 0px;
    }
    .form-input {
        width: 85%;
    }
    .tokens {
        position: absolute;
        left: 0px;
        top: -272px;
        width: 254px;
    }
    .tokens-right {
        position: absolute;
        right: 8px;
        top: 433px;
        width: 254px;
    }
    
}

@media screen and (max-width:550px) {
    .tokens {
        position: absolute;
        left: 0px;
        top: -195px;
        width: 200px;
    }
}

@media screen and (max-width: 500px) {
    .tokens {
        position: absolute;
        left: 0px;
        top: -173px;
        width: 165px;
    }
    .tokens-right {
        position: absolute;
        right: 8px;
        top: 501px;
        width: 191px;
    }
    .form{
        padding-bottom: 230px;
        border-bottom: 1px solid white;
    }
}

@media screen and (max-width:415px) {
    .form__wrapper{
        margin-top: 62px;
    }

    .form__title h1 {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 700;
        font-size: 21px;
        line-height: 61px;
        margin: 0;
        color: #FFFFFF;
    }

    .form-input{
        width: 80%;
        padding-left: 12px;
        padding-bottom: 8px;
        padding-top: 22px;
    }

    .form{
        margin: 0;
    }
    .form-input::placeholder{


font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 16px;


/* main/text/2 */
color: #959595;


    }

    .form-btn{
        padding: 0px 40px;
        font-size: 19px;
        padding-top: 4px;
        padding-bottom: 3px;
        margin-top: 11px;
        position: relative;
    }

    .tokens {
       display: none;
    }
    .tokens-right {
        display: none;
    }

    .form{
        padding-bottom: 57px;
    }
    
}