    border-bottom: 1px solid #fff;
    display: flex;
    align-items: flex-end;
    margin-left: -30px;
    margin-top: 16px;
}
.product3 .name {
    bottom: 21px;
    font-family: "Chakra Petch";
font-style: normal;
font-weight: 400;
font-size: 10px;
line-height: 180%;
color: #FFFFFF;
text-transform: uppercase;
transform: rotate(90deg);
position: absolute;
right: -12px;

}

.product3 .corner__top {
    top: 0px;
    width: 41px;
    right: -1px;
}
.product3 .corner {
    left: -15px;
    bottom: 49px;
}
}

@media screen and (max-width:350px) {
    .product1 .product__left img {
        max-width: 315px;
    }
    .product2 .product__left img {
        max-width: 315px;
    }
    .product2 .product__right {
        margin-top: -4px;
        margin-left: 50px;
    }
    .product2 .product__subtitle {
        margin-left: -4px;
        letter-spacing: -0.07px;
        margin-top: 9px;
        font-size: 13px;
        text-align: center;
        max-width: 293px;
    }
    .product2 .corner {
        left: -13px;
        bottom: 52px;
    }
    .product3 .name {
        right: -6px;
    }
    .produc23 .name {
        right: -6px;
    }
    .product1 .name {
        right: -6px;
    }
}